import React, { useEffect } from 'react';
import styles from './styles.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { getArtistDetails } from '../../../../../redux/booking/artist/actions';
import Code from './code/Code';
import Date from './date/Date';
import Services from './services/Services';
import PaymentInfo from './payment-info/PaymentInfo';
import Artist from './artist/Artist';
import Price from './price/Price';


const Body = () => {
    
    const dispatch = useDispatch();

    const { book: { total, artist } } = useSelector(state => state.customerDashboardBookDetails);


    useEffect(() => {
        artist && dispatch(getArtistDetails(artist));
    }, [artist]);
    

    return (
        <div className={styles.body}>
            
            <div className = {styles.msg}>

                <svg xmlns="http://www.w3.org/2000/svg" 
                width = '35'
                height = '35' 
                fill = '#00d166'
                class="bi bi-check-circle" viewBox="0 0 16 16">
                <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                <path d="M10.97 4.97a.235.235 0 0 0-.02.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-1.071-1.05z"/>
                </svg>
                
                <span>موفق</span>

            </div>

            <Code />

            <Date />

            <div className={styles.payment}>

                <div>
                    <span>نوع پرداخت</span>
                    <span>حضوری</span>
                </div>

                <Price value = {total && total.price} />
                
            </div>
            
            <Artist />
            
            <Services />
            
        </div>
    );
};

export default Body;